<template>
  <div>
    <h1>    ( )</h1>
    <input v-model="stockSymbol" placeholder="  " />
    <button @click="fetchStockPrice"></button>
    <p v-if="stockPrice !== null">{{ stockSymbol }}  : {{ stockPrice }}</p>
    <p v-if="errorMessage">{{ errorMessage }}</p>
  </div>
</template>

<script>
import axios from "axios";
import cheerio from "cheerio"; //     cheerio

export default {
  data() {
    return {
      stockSymbol: "", //    
      stockPrice: null,
      errorMessage: "",
    };
  },
  methods: {
    
    async fetchStockPrice() {
      if (!this.stockSymbol) {
        this.errorMessage = "  .";
        return;
      }

      try {
        //   URL
        const url = `https://finance.naver.com/item/main.nhn?code=${this.stockSymbol}`;
        

        //    HTML 
        const response = await axios.get(url, {
          headers: {
            'User-Agent': 'Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/91.0.4472.124 Safari/537.36',
            'Referer': '',
            'Origin' : ''
          }
        });
        const html = response.data;

        // cheerio  HTML 
        const $ = cheerio.load(html);

        //    (    CSS  )
        const stockPrice = $("p.no_today .blind").first().text();

        if (stockPrice) {
          this.stockPrice = stockPrice.replace(",", ""); //  
          this.errorMessage = "";
        } else {
          this.errorMessage = "     .";
        }
      } catch (error) {
        this.errorMessage = "    .";
        console.error(error);
      }
    },
  },
};
</script>

<style>
input {
  padding: 10px;
  margin-right: 10px;
}
button {
  padding: 10px;
}
p {
  margin-top: 20px;
}
</style>
