<template>
  <div>
    <h1>  </h1>
    <form @submit.prevent="buyStock">
      <div>
        <label>:</label>
        <select v-model="stock_symbol">
          <option value="A">A </option>
          <option value="B">B </option>
        </select>
      </div>
      <div>
        <label>:</label>
        <input type="date" v-model="trade_date" />
      </div>
      <div>
        <label> :</label>
        <input type="number" v-model="price" />
      </div>
      <div>
        <label>:</label>
        <input type="number" v-model="quantity" />
      </div>
      <div>
        <label> :</label>
        <input type="number" v-model="total_amount" />
      </div>
      <button type="submit"> </button>
    </form>
  </div>
</template>

<script>
import Cookies from 'js-cookie';

export default {
  data() {
    return {
      stock_symbol: '',
      trade_date: '',
      price: 0,
      quantity: 0,
      total_amount: 0,
      transactions: [] //    
    };
  },
  methods: {
    buyStock() {
      //    
      const newTransaction = {
        stock_symbol: this.stock_symbol,
        trade_date: this.trade_date,
        price: this.price,
        quantity: this.quantity,
        total_amount: this.total_amount
      };

      //     
      const savedTransactions = Cookies.get('transactions');
      this.transactions = savedTransactions ? JSON.parse(savedTransactions) : [];

      //      
      this.transactions.push(newTransaction);
      Cookies.set('transactions', JSON.stringify(this.transactions), { expires: 36500 }); //  100 

      alert('   .');
    }
  }
};
</script>
