<template>
  <div id="app">
    <h1>  </h1>

    <div>
      <label> :</label>
      <select v-model="selectedStock">
        <option disabled value=""> </option>
        <option v-for="stock in stocks" :key="stock" :value="stock">
          {{ stock }}
        </option>
      </select>
    </div>

    <div>
      <label>:</label>
      <input type="number" v-model="quantity" placeholder=" " />
    </div>

    <div>
      <label>:</label>
      <input type="number" v-model="price" placeholder=" " />
    </div>

    <div>
      <label> :</label>
      <input type="date" v-model="date" />
    </div>

    <button @click="saveTransaction"> </button>
    <button @click="clearForm"></button>

    <h2></h2>
    <table>
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(transaction, index) in transactions" :key="index">
          <td>{{ transaction.stock }}</td>
          <td>{{ transaction.quantity }}</td>
          <td>{{ transaction.price }}</td>
          <td>{{ transaction.date }}</td>
          <td><button @click="editTransaction(index)"></button></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import Cookies from "js-cookie";

export default {
  data() {
    return {
      selectedStock: "",
      quantity: 0,
      price: 0,
      date: "",
      transactions: [],
      stocks: ["A ", "B "],
      editIndex: null, //   
    };
  },
  mounted() {
    this.loadTransactions(); //   
  },
  methods: {
    saveTransaction() {
      if (!this.selectedStock || this.quantity <= 0 || this.price <= 0 || !this.date) {
        alert("   .");
        return;
      }

      const newTransaction = {
        stock: this.selectedStock,
        quantity: this.quantity,
        price: this.price,
        date: this.date,
      };

      if (this.editIndex !== null) {
        //   
        this.transactions.splice(this.editIndex, 1, newTransaction);
        this.editIndex = null;
      } else {
        //   
        this.transactions.push(newTransaction);
      }

      this.saveToCookies();
      this.clearForm();
    },
    editTransaction(index) {
      const transaction = this.transactions[index];
      this.selectedStock = transaction.stock;
      this.quantity = transaction.quantity;
      this.price = transaction.price;
      this.date = transaction.date;
      this.editIndex = index; //    
    },
    clearForm() {
      this.selectedStock = "";
      this.quantity = 0;
      this.price = 0;
      this.date = "";
      this.editIndex = null;
    },
    saveToCookies() {
      // JSON   
      Cookies.set("transactions", JSON.stringify(this.transactions));
    },
    loadTransactions() {
      const cookieData = Cookies.get("transactions");
      if (cookieData) {
        this.transactions = JSON.parse(cookieData);
      }
    },
  },
};
</script>

<style scoped>
table {
  width: 100%;
  border-collapse: collapse;
}
th, td {
  border: 1px solid #ddd;
  padding: 8px;
}
th {
  background-color: #f2f2f2;
}
</style>
