<template>
  <div class="profit-container">
    <h2>   </h2>
    <table class="profit-table">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in salesData" :key="index">
          <td>{{ item.stock_name }}</td>
          <td>{{ item.buy_date }}</td>
          <td :class="item.profit >= 0 ? 'positive' : 'negative'">
            {{ Number(item.profit).toLocaleString() }}
          </td>
          <td>{{ Number(item.buy_price).toLocaleString() }}</td>
          <td>{{ item.buy_qty }}</td>
          <td>{{ item.sell_date }}</td>
          <td>{{ Number(item.sell_price).toLocaleString() }}</td>
          <td>{{ item.sell_qty }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "SalesProfit",
  data() {
    return {
      salesData: [],
    };
  },
  async mounted() {
    try {
      const response = await fetch("https://dodamee.cloud/getSalesProfit", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({}) //     
      });

      const data = await response.json();
      this.salesData = data;
    } catch (error) {
      console.error("   :", error);
    }
  },
};
</script>

