<template>
  <div class="summary-container">
    <h2>   ( / )</h2>

    <section class="summary-section">
      <h3>  </h3>
      <table class="summary-table">
        <thead>
          <tr>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in yearlyProfits" :key="index">
            <td>{{ item.year }}</td>
            <td :class="item.profit >= 0 ? 'positive' : 'negative'">
              {{ Number(item.profit).toLocaleString() }}
            </td>
          </tr>
        </tbody>
      </table>
    </section>

    <section class="summary-section">
      <h3>  </h3>
      <table class="summary-table">
        <thead>
          <tr>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in monthlyProfits" :key="index">
            <td>{{ item.month }}</td>
            <td :class="item.profit >= 0 ? 'positive' : 'negative'">
              {{ Number(item.profit).toLocaleString() }}
            </td>
          </tr>
        </tbody>
      </table>
    </section>
  </div>
</template>

<script>
export default {
  name: "SalesSummary",
  data() {
    return {
      salesData: [],
    };
  },
  async mounted() {
    try {
      const response = await fetch("https://dodamee.cloud/getSalesProfit", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({}) //     
      });
      const data = await response.json();
      this.salesData = data;
    } catch (error) {
      console.error("   :", error);
    }
  },
  computed: {
    yearlyProfits() {
      const grouped = {};
      this.salesData.forEach((item) => {
        const year = new Date(item.sell_date).getFullYear();
        grouped[year] = (grouped[year] || 0) + item.profit;
      });

      return Object.entries(grouped)
        .map(([year, profit]) => ({ year, profit }))
        .sort((a, b) => b.year - a.year);
    },

    monthlyProfits() {
      const grouped = {};
      this.salesData.forEach((item) => {
        const d = new Date(item.sell_date);
        const key = `${d.getFullYear()}-${String(d.getMonth() + 1).padStart(2, "0")}`;
        grouped[key] = (grouped[key] || 0) + item.profit;
      });

      return Object.entries(grouped)
        .map(([month, profit]) => ({ month, profit }))
        .sort((a, b) => b.month.localeCompare(a.month));
    },
  },
};
</script>

