<template>
  <div>
    <h1>    (Yahoo Finance)</h1>
    <input v-model="stockSymbol" placeholder="  " />
    <button @click="fetchStockPrice"></button>
    <p v-if="stockPrice !== null">{{ stockSymbol }}  : {{ stockPrice }}</p>
    <p v-if="errorMessage">{{ errorMessage }}</p>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      stockSymbol: "", //    
      stockPrice: null,
      errorMessage: "",
    };
  },
  methods: {
    async fetchStockPrice() {
      if (!this.stockSymbol) {
        this.errorMessage = "  .";
        return;
      }

      try {
        // Yahoo Finance API 
        const response = await axios.get(
          `https://query1.finance.yahoo.com/v7/finance/quote?symbols=${this.stockSymbol}.KS`
        );
        const stockData = response.data.quoteResponse.result[0];

        if (stockData) {
          this.stockPrice = stockData.regularMarketPrice;
          this.errorMessage = "";
        } else {
          this.errorMessage = "     .";
        }
      } catch (error) {
        this.errorMessage = "    .";
        console.error(error);
      }
    },
  },
};
</script>

<style>
input {
  padding: 10px;
  margin-right: 10px;
}
button {
  padding: 10px;
}
p {
  margin-top: 20px;
}
</style>
